html {
  font-family: 'Montserrat', sans-serif;
  scroll-behavior: smooth;
}

img {
  max-width: 100%;
}

p {
  margin: 0 auto;
}

.container {
  max-width: 1200px;
}

.bg-green {
  background: #52C41A;
}

.header {
  position: absolute;
  width: 100%;
  padding: 8px 0;
}

.header__formLink {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: white;
  text-decoration: none;
  margin-top: 8px;
}

.logo {
  color: #FFFFFF;
}

.logo__name {
  font-weight: 700;
  font-size: 30px;
  line-height: 37px;
}

.logo__name span {
  color: #FA8C16;
}

.logo__description {
  text-transform: uppercase;
  font-weight: 500;
  font-size: 10px;
  line-height: 12px;
  letter-spacing: 0.15em;
}

.blockMain {
  color: white;
  background: #2F54EB;
  min-height: 550px;
}

@media (max-width: 900px) {
  .blockMain {
    padding-top: 76px;
    padding-bottom: 52px;
  }
}

.blockMain__title {
  font-weight: 600;
  font-size: 35px;
  line-height: 48px;
}

@media (max-width: 900px) {
  .blockMain__title {
    font-size: 23px;
    line-height: 28px;
  }
}

.blockMain__description {
  font-weight: 500;
  font-size: 23px;
  line-height: 32px;
  padding-bottom: 16px;
}

@media (max-width: 900px) {
  .blockMain__description {
    font-size: 14px;
    line-height: 20px;
  }
}

.features {
  position: relative;
}

@media (max-width: 900px) {
  .features {
    padding: 44px 16px;
  }
}

.features__item {
  padding: 48px;
}

@media (max-width: 900px) {
  .features__item {
    padding: 0 0 24px;
  }
}

.features__title {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
}

@media (max-width: 900px) {
  .features__title {
    padding-left: 16px;
  }
}

@media (min-width: 768px) {
  .features__surprise {
    position: absolute;
    bottom: -60px;
    left: 50%;
    transform: translate(-50%, 100%);
    min-width: 504px;
    border: 0 !important;
  }
}

.zipForm {
  margin-bottom: 44px;
}

.zipForm__container {
  background: #2F54EB;
  background-size: cover;
  border-radius: 24px;
  padding: 24px;
}

@media (min-width: 768px) {
  .zipForm__container {
    padding: 120px 96px 96px;
    background: #2F54EB url(imgs/zip-form-bg.e74778a757e86b196f69.png) center center no-repeat;
  }
}

.zipForm__title {
  font-weight: 700;
  font-size: 25px;
  line-height: 36px;
  color: white;
  margin: 0 auto 16px;
}

.zipForm__formContent {
  max-width: 320px;
}

.surprise {
  border: 1px solid #FA8C16;
  border-radius: 8px;
  padding: 8px;
  background: #F5F6FE;
  margin: 0 auto;
}

.surprise__text {
  padding: 0 8px;
  margin: 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
}

.form {
  background: white;
  padding: 24px 28px;
  border-radius: 16px;
}

.form__title {
  font-weight: 500;
  font-size: 23px;
  line-height: 32px;
  color: black;
  margin: 0 auto 16px;
}

.form__input {
  width: 100%;
  margin-bottom: 8px;
}

.form__fieldset {
  position: relative;
  margin: 0 auto;
  border: 0;
  padding: 0;
}

.form__description {
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #646464;
}

.form__button {
  width: 100%;
  margin: 16px auto 0;
}

.label {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #222222;
  position: absolute;
  margin: 19px 16px;
  pointer-events: none;
  left: 0;
}

.input {
  background: #F5F6FE;
  border-radius: 4px;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #222222;
  border: 1px solid transparent;
  padding: 10px 16px;
  height: 56px;
}

.input:focus {
  outline: none;
  border: 1px solid #2F54EB;
}

.input:focus + .label {
  font-size: 11px;
  line-height: 13px;
  color: #2F54EB;
  margin-top: 4px;
}

.input::placeholder {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #646464;
}

.footer {
  background: #2F54EB;
  padding: 24px 0;
}

.footer__menu {
  list-style: none;
  margin: 24px 0 0;
  padding: 0;
  max-width: 230px;
}

.footer__menu li {
  padding: 0 0 16px;
}

@media (min-width: 992px) {
  .footer__menu {
    width: 100%;
    max-width: 100%;
    justify-content: center;
  }
  .footer__menu li {
    padding: 0 16px 16px;
  }
}

.menu__link {
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  color: white;
  text-decoration: none;
}

.button {
  background: #FA8C16;
  border-radius: 4px;
  border: 0;
  padding: 10px 24px;
  height: 56px;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: white;
}

.button:not(.button_disabled):hover {
  cursor: pointer;
  background: #F16E00;
}

.button_disabled {
  background: #FCC58A;
}

.consumer {
  position: fixed;
  width: 100%;
  min-height: 100%;
  background: white;
  left: 0;
  top: 0;
}

.consumer_invisible {
  display: none;
}

.consumer__progressbar {
  height: 4px;
  width: 50%;
  background: #52C41A;
  position: absolute;
  top: 72px;
  left: 0;
}

.consumer__title {
  font-weight: 700;
  font-size: 25px;
  line-height: 36px;
  color: #222222;
}

.consumer__container {
  margin-top: 172px;
  max-width: 800px;
}

.consumer__choseText {
  margin-bottom: 24px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #646464;
}

.closeIcon {
  display: block;
  width: 24px;
  height: 24px;
  background: url(data:image/svg+xml;base64,) center no-repeat;
  background-size: cover;
  position: absolute;
  top: 24px;
  right: 40px;
}

.choseForm {
  margin: 0 auto;
}

.choseForm__control {
  width: 168px;
  height: 140px;
  border: 2px solid #F5F6FE;
  border-radius: 16px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  margin: 0 8px 24px;
  cursor: pointer;
}

.choseForm__control:hover {
  border-color: #FA8C16;
}

.choseForm__input {
  width: 0;
  height: 0;
  opacity: 0;
}

.choseForm__icon {
  margin-bottom: 16px;
}

.userForm__fieldset {
  position: relative;
  max-width: 250px;
  border: 0;
  padding: 0;
  margin: 0 auto;
}

.userForm__title {
  margin-bottom: 4px;
}

.userForm__input {
  margin-bottom: 16px;
}

.userForm__securityText {
  font-weight: 500;
  font-size: 14px;
  line-height: 23px;
  color: #646464;
}

.userForm__button {
  margin-bottom: 16px;
}

.userForm__securityText {
  background: url(data:image/svg+xml;base64,) center left no-repeat;
  padding-left: 24px;
}

